@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/montserratregular.woff2") format("woff2"), url("/fonts/montserratregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("/fonts/montserratmedium.woff2") format("woff2"), url("/fonts/montserratmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("/fonts/montserratsemibold.woff2") format("woff2"), url("/fonts/montserratsemibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/montserratbold.woff2") format("woff2"), url("/fonts/montserratbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
body {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #333333;
  background-color: #ffffff;
  min-width: 320px;
  max-width: 2560px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  body {
    font-size: 20px;
    line-height: 32px;
  }
}
.button {
  max-width: 150px;
  padding: 8px 20px;
  background-color: #7859cf;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #ffffff;
  word-wrap: break-word;
}
.button:hover {
  color: #ffffff;
  background-color: #9070ec;
}
.button:focus-visible {
  outline: none;
  color: #ffffff;
  background-color: #9070ec;
}
.button:active {
  color: #ffffff;
  background-color: #593cac;
}
.button:disabled {
  color: #ffffff;
  background-color: #bdbdbd;
}
.button--reset {
  color: #7859cf;
  background-color: #ffffff;
}
.button--reset:disabled {
  background-color: #e2e2e2;
}
.button--cart {
  position: relative;
  padding: 8px 20px 8px 46px;
  font-size: 14px;
  line-height: 20px;
}
.button--cart::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 16px;
  top: 10px;
  left: 20px;
  background: url("/img/icons/cart.svg") center no-repeat;
}
.slider {
  position: relative;
}
.slider__item {
  display: none;
}
.slider__item--active {
  display: block;
}
.slider__toggle {
  position: absolute;
  width: 20px;
  height: 100%;
  top: 0;
  background-color: transparent;
  border: none;
  color: #7859cf;
}
@media (min-width: 768px) {
  .slider__toggle {
    width: 80px;
  }
}
.slider__toggle--back {
  left: calc((100% - 320px) / 2);
}
@media (min-width: 768px) {
  .slider__toggle--back {
    left: calc((100% - 768px) / 2);
  }
}
@media (min-width: 1440px) {
  .slider__toggle--back {
    left: calc((100% - 1440px) / 2);
  }
}
.slider__toggle--back:hover,
.slider__toggle--back:focus-visible {
  outline: none;
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}
.slider__toggle--back:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}
.slider__toggle--forward {
  right: calc((100% - 320px) / 2);
}
@media (min-width: 768px) {
  .slider__toggle--forward {
    right: calc((100% - 768px) / 2);
  }
}
@media (min-width: 1440px) {
  .slider__toggle--forward {
    right: calc((100% - 1440px) / 2);
  }
}
.slider__toggle--forward:hover,
.slider__toggle--forward:focus-visible {
  outline: none;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
}
.slider__toggle--forward:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0) 0%, rgba(120, 89, 207, 0.25) 100%);
}
.slider__toggle:disabled {
  color: #bdbdbd;
  background: transparent;
}
.slider__pagination {
  margin: 0;
  padding: 0;
  list-style: none;
}
.slider__pagination-item {
  margin-left: 8px;
  line-height: 0;
}
.slider__pagination-item--active .slider__pagination-button {
  background-color: #7859cf;
}
.slider__pagination-button {
  width: 62px;
  height: 4px;
  box-sizing: border-box;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}
.slider__pagination-button:hover,
.slider__pagination-button:focus-visible {
  outline: none;
  background-color: rgba(120, 89, 207, 0.25);
}
.section-title {
  margin: 0 0 5px;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #7859cf;
  text-align: center;
}
.section-secondary-title {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  text-align: center;
  margin: 0 0 36px;
}
@media (min-width: 768px) {
  .section-secondary-title {
    font-size: 42px;
    line-height: 64px;
    margin-bottom: 60px;
  }
}
.sorting {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
.sorting__text {
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}
.sorting__control {
  max-width: 176px;
  min-height: 32px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #7859cf;
  text-align: right;
  background-color: transparent;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.sorting__control:focus,
.sorting__control:hover {
  outline: none;
  background-color: #f0ebff;
}
.card {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  padding: 40px 20px 20px;
  background-color: #ffffff;
  border-radius: 4px;
}
.card:hover,
.card:focus,
.card:focus-within {
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.15);
}
@media (min-width: 768px) {
  .card {
    margin-bottom: 0;
    padding: 50px 30px 30px;
  }
}
.card__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #333333;
  text-decoration: none;
}
.card__link:focus {
  outline: none;
}
.card__image {
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .card__image {
    margin-bottom: 30px;
  }
}
.card__title {
  margin: 0 0 4px;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  align-self: flex-start;
}
@media (min-width: 768px) {
  .card__title {
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 36px;
  }
}
.card__description {
  margin: 0 0 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .card__description {
    margin-bottom: 20px;
  }
}
.card__price-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card__price {
  max-width: 99px;
  font-weight: 600;
  font-size: 20px;
  line-height: 32px;
  word-wrap: break-word;
}
@media (min-width: 768px) {
  .card__price {
    max-width: 148px;
  }
}
@media (min-width: 1440px) {
  .card__price {
    max-width: 116px;
  }
}
.main-header {
  width: 100%;
}
.main-nav {
  margin: 0 auto;
  width: 320px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .main-nav {
    width: 768px;
    padding: 0 20px;
  }
}
@media (min-width: 1440px) {
  .main-nav {
    width: 1440px;
    padding: 0 20px 0 40px;
  }
}
.main-nav__logo {
  display: flex;
  font-size: 12px;
  line-height: 0;
  color: #333333;
  text-decoration: none;
}
.main-nav__logo:focus {
  outline: none;
}
.main-nav__logo-image {
  margin: 12px 20px;
}
@media (min-width: 768px) {
  .main-nav__logo-image {
    width: 82px;
    margin: 22px 4px 22px 0;
  }
}
@media (min-width: 1440px) {
  .main-nav__logo-image {
    width: 170px;
    margin: 22px 0;
  }
}
.main-nav__logo-text {
  display: none;
}
@media (min-width: 1440px) {
  .main-nav__logo-text {
    display: block;
    margin: 24px 40px 24px 20px;
    min-width: 126px;
    line-height: 16px;
  }
}
.menu {
  display: grid;
  grid-template-columns: 72px 1fr;
}
@media (min-width: 768px) {
  .menu {
    display: flex;
  }
}
.menu__main-menu-wrapper {
  grid-area: 2/1/3/3;
}
@media (min-width: 768px) {
  .menu__main-menu-wrapper {
    display: block;
    width: 100%;
  }
}
.menu__main-menu-wrapper--close {
  display: none;
}
@media (min-width: 768px) {
  .menu__main-menu-wrapper--close {
    display: block;
  }
}
.menu__login-menu-wrapper {
  display: flex;
  justify-content: flex-end;
}
.menu__main-menu-list,
.menu__login-menu-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.menu__main-menu-list {
  display: flex;
  flex-direction: column;
  align-content: center;
}
@media (min-width: 768px) {
  .menu__main-menu-list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
}
.menu__login-menu-list {
  display: flex;
  flex-wrap: wrap;
  min-width: 120px;
}
@media (min-width: 1440px) {
  .menu__login-menu-list {
    justify-content: flex-end;
    min-width: 300px;
  }
}
.menu__main-menu-item {
  text-align: center;
  box-sizing: border-box;
  border-top: 1px #f0ebff solid;
}
@media (min-width: 768px) {
  .menu__main-menu-item {
    border: none;
  }
}
@media (min-width: 1440px) {
  .menu__main-menu-item {
    margin-right: 20px;
  }
}
.menu__main-menu-item:last-child {
  border-bottom: 1px #f0ebff solid;
}
@media (min-width: 768px) {
  .menu__main-menu-item:last-child {
    border: none;
  }
}
.menu__login-menu-item {
  text-align: center;
}
.menu__main-menu-link,
.menu__login-menu-link {
  display: block;
  padding: 20px;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  color: #333333;
  text-align: center;
  letter-spacing: 0.02em;
}
@media (min-width: 768px) {
  .menu__main-menu-link--current,
  .menu__login-menu-link--current {
    color: #7859cf;
    border-bottom: 2px solid #7859cf;
  }
  .menu__main-menu-link:hover,
  .menu__login-menu-link:hover,
  .menu__main-menu-link:focus-visible,
  .menu__login-menu-link:focus-visible {
    color: #7859cf;
  }
}
.menu__main-menu-link:focus,
.menu__login-menu-link:focus {
  outline: none;
}
.menu__main-menu-link:active,
.menu__login-menu-link:active {
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .menu__main-menu-link {
    padding: 32px 20px 30px;
  }
}
.menu__login-menu-link {
  line-height: 0;
}
@media (min-width: 768px) {
  .menu__login-menu-link {
    padding: 30px 20px;
  }
}
@media (min-width: 1440px) {
  .menu__login-menu-link {
    display: flex;
    align-items: center;
    margin-right: 20px;
  }
}
.menu__login-menu-link svg {
  min-width: 20px;
  min-height: 20px;
}
.menu__login-menu-text {
  display: none;
  margin: 0;
}
@media (min-width: 1440px) {
  .menu__login-menu-text {
    display: block;
    margin-left: 6px;
    line-height: 16px;
  }
}
.menu__toggle {
  display: none;
  position: relative;
  width: 60px;
  height: 60px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.menu__toggle:focus {
  outline: none;
}
.menu__toggle:active {
  background-color: #f0ebff;
}
.menu__toggle--JS-menu {
  display: block;
}
@media (min-width: 768px) {
  .menu__toggle--JS-menu {
    display: none;
  }
}
.menu__toggle-burger {
  display: block;
  box-sizing: border-box;
  position: relative;
  left: 20px;
  width: 20px;
  height: 2px;
  background-color: #333333;
  transition-duration: 0.25s;
  transition-delay: 0.25s;
}
.menu__toggle-burger::after,
.menu__toggle-burger::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 2px;
  left: 0;
  background-color: #333333;
  transition: transform 0.25s, top 0.25s 0.25s;
  transition-duration: 0.25s;
}
.menu__toggle-burger::after {
  top: -6px;
}
.menu__toggle-burger::before {
  top: 6px;
}
.menu__toggle-burger--close {
  transition-duration: 0.1s;
  transition-delay: 0.25s;
  background: transparent;
}
.menu__toggle-burger--close::after {
  transition: top 0.4s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(45deg);
}
.menu__toggle-burger--close::before {
  transition: top 0.25s, transform 0.25s 0.25s;
  top: 0;
  transform: rotateZ(-45deg);
}
.main-page {
  width: 100%;
}
.new__list {
